.customer-page {
  text-align: center;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.order-form {
  margin-top: 40px;
}

.section-title {
  font-size: 18px;
  margin-bottom: 10px;
}

.form-field {
  margin-bottom: 10px;
}

.form-field label {
  display: block;
  margin-bottom: 5px;
}

.form-field input {
  width: 100%;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.submit-button {
  background-color: #28a745;
  color: #fff;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  margin-top: 10px;
}

.message {
  margin-top: 10px;
  color: #dc3545;
}

.options {
  margin-top: 40px;
}

.back-button {
  background-color: #dc3545;
  color: #fff;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
}
